import React from 'react';
import './App.css';
import KeyboardEventHandler from 'react-keyboard-event-handler';

export const App = () => {
  let time = "00:00", text = "Start", id = null;
  const timer = () => {
    const val = setInterval(()=> {
      let [min, sec] = time.split(":");
      min = parseInt(min); sec = parseInt(sec);
      if(min === 5){
        document.getElementById("time").style.color = "red";
        document.getElementById("button").innerHTML = text = "Reset";
        clearInterval(id); 
        return;
      }
      sec++; if(sec === 60){ sec = 0; min++;}
      min = min.toString().padStart(2, '0');
      sec = sec.toString().padStart(2, '0');
      time = `${min}:${sec}` 
      document.getElementById("time").innerHTML = time;     
    },1000)
    return val;
  }

  const handleClick = (button) => {  
    switch(text){
      case "Start": id = timer(); text = "Pause"; break;  
      case "Pause": clearInterval(id); text = "Start"; break;
      case "Reset":
        const timetext = document.getElementById("time");
        text = "Start" ; time = "00:00"; 
        timetext.style.color = "black"; timetext.innerHTML = time; 
             
    }
    button.innerHTML = text;
  }

  const handleKey = (key) =>{
    let selected = null, other = null;
    const boxes = document.getElementsByClassName("boxes");
    [...boxes].forEach((box, index)=> {
      if(box.classList.contains("selected")) selected = box;
      else other = box;
    })
    if(selected != null ){
      let st = parseInt(getComputedStyle(selected).getPropertyValue("top").replace("px",""))
      let sl = parseInt(getComputedStyle(selected).getPropertyValue("left").replace("px",""))
      let ot = parseInt(getComputedStyle(other).getPropertyValue("top").replace("px",""))
      let ol = parseInt(getComputedStyle(other).getPropertyValue("left").replace("px",""))
      switch(key){
        case "up" : st -= 1;break;
        case "down" : st += 1;break;
        case "left" : sl -= 1;break;
        case "right" : sl += 1;break;
      }
      if( st < 0 || st > 500 || sl < 0 || sl > 500 || 
        (Math.abs(st - ot) === 100 && Math.abs(sl - ol) <= 100) ||
        (Math.abs(sl - ol) === 100 && Math.abs(st - ot) <= 100)) {
        return;
          }
      selected.style.top = st.toString() + "px";
      selected.style.left = sl.toString() + "px";
    }    
  }

  const selectBox = (box) => {
      const boxes = document.getElementsByClassName("boxes");
      [...boxes].forEach((item) => {
        if(item.classList.contains("selected")) item.classList.remove("selected")
      })
      box.classList.add("selected");
  }

  const switchTab = (e, index) => {
    [...document.getElementsByClassName("nav")]
    .forEach((span) => span.style.borderBottom = "none")
    e.style.borderBottom = "5px solid #00AE86";
    const disp1 = (index === 1) ? "block" : "none";
    const disp2 = (index === 2) ? "block" : "none";
    document.getElementById("p1").style.display = disp1;
    document.getElementById("p2").style.display = disp2;
  }
  
  return (
    <div id="app" >
    <div id = "nav">
    <span className = "nav" onClick = {e => {switchTab(e.target, 1)}}> Problem 1</span>
    <span className = "nav" onClick = {e => {switchTab(e.target, 2)}}>Problem 2</span>
    </div>
    <div id = "p1">
      <div><span id = "title">Privado Timer</span></div>
      <div><span id = "time">{time}</span></div>
      <div><button id = "button" onClick = {e => handleClick(e.target)}>{text}</button></div>
    </div> 
    <div id = "p2">
      <KeyboardEventHandler 
        handleKeys={['up', 'down', 'left', 'right']} 
        onKeyEvent={(key) => handleKey(key)} />
        <div className = "boxes" id = "b1" onClick ={e => selectBox(e.target)}/>
        <div className = "boxes" id = "b2" onClick ={e => selectBox(e.target)}/>
      </div>
    </div>
  );
}

